import request from "../utils/request"; // 获取列表

export var getAllWork = function getAllWork(query) {
  return request({
    url: 'get_all_work',
    method: 'get',
    params: query
  });
}; // 删除

export var deleteWork = function deleteWork(query) {
  return request({
    url: 'del_work',
    method: 'get',
    params: query
  });
}; // 编辑

export var editWork = function editWork(query) {
  return request({
    url: 'edit_work',
    method: 'post',
    data: query
  });
}; // 新建

export var addWork = function addWork(query) {
  return request({
    url: 'create_work',
    method: 'post',
    data: query
  });
};